import React from 'react';
import PropTypes from 'prop-types';

export default function Form() { onSubmit, currentUser, analitics, disabled } {

  const [formData, setFormData] = useState(analitics);

  return (
    <form onSubmit={onSubmit}>
      <fieldset id="fieldset">
        <p>Sign into log analitics, {currentUser.accountId}!</p>
        
        <p>
          <label htmlFor="app_id">App #</label>
          <input
            autoComplete="off"
            autoFocus
            name="app_id"
            id="app_id"
            value={ formData.app_id }
            required />
        </p>
        <p>
          <label htmlFor="action_id">Action #</label>
          <input
            autoComplete="off"
            name="action_id"
            id="action_id"
            value={ formData.action_id }
            required />
        </p>
        <p>
          <label htmlFor="user_name">User name</label>
          <input
            autoComplete="off"
            name="user_name"
            id="user_name"
            value={ formData.user_name }
            required />
        </p>

        <button type="submit">
          Submit
        </button>
      </fieldset>
    </form>
  );
  
}

Form.propTypes = {
  onSubmit: PropTypes.func.isRequired,
  currentUser: PropTypes.shape({
    accountId: PropTypes.string.isRequired,
    balance: PropTypes.string.isRequired
  }),
  analitics: PropTypes.shape({
    app_id: PropTypes.string.isRequired,
    action_id: PropTypes.string.isRequired,
    user_name: PropTypes.string.isRequired
  })
};
